<template>
  <div ref="chart"></div>
</template>

<script>
import { Gauge } from '@antv/g2plot'
export default {
  mounted () {
    const { $refs: { chart: chartEl } } = this
    // this.chartInit()
    const gaugePlot = new Gauge(chartEl, {
      height: 200,
      width: chartEl.clientWidth,
      value: 64,
      min: 0,
      max: 100,
      gaugeStyle: {
        colors: ['#f4664a', '#faad14', '#a0d911', '#30bf78'],
        tickLabelSize: 20
      },
      statistic: () => {
        return '<div class="g2plot-gauge-label"><p class="title">优</p></div>'
      }
    })
    gaugePlot.render()
  },
  methods: {
  }
}
</script>

