<template>
  <div ref="chart"></div>
</template>

<script>
import { Liquid } from '@antv/g2plot'
export default {
  mounted () {
    const { $refs: { chart: chartEl } } = this
    const liquidPlot = new Liquid(chartEl, {
      height: 200,
      min: 0,
      max: 10000,
      value: 5639,
      statistic: {
        formatter: (value) => ((100 * value) / 10000).toFixed(1) + '%'
      }
    })
    liquidPlot.render()
  }
}
</script>

