<template>
  <div ref="chart" :style="{width: width, height: height}"></div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '220px'
    }
  },
  mounted () {
    this.drawChart()
  },
  methods: {
    drawChart () {
      const chartEl = this.$echarts.init(this.$refs.chart)

      chartEl.setOption({
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['总数', '待处理', '完成率']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: ['证明函管理', '收发室', '车辆管理', '司机管理']
        },
        yAxis: [
          {
            type: 'value',
            scale: true,
            name: '总数',
            min: 0
          },
          {
            type: 'value',
            scale: true
          },
          {
            type: 'value',
            scale: true,
            name: '转化率',
            max: 100,
            min: 0,
            boundaryGap: [0, 100]
          }
        ],
        series: [
          {
            name: '总数',
            type: 'bar',
            data: [120, 132, 101, 134]
          },
          {
            name: '待处理',
            type: 'bar',
            data: [110, 112, 80, 120]
          },
          {
            name: '完成率',
            type: 'line',
            data: [20, 34, 56, 78]
          }
        ]
      })
    }
  }
}
</script>

